let cleanRoom = function(){
    return new Promise((resolve, reject)=>{
        resolve('Cleaned the room');
    })
}

let removeGarbage = function(p){
    return new Promise((resolve, reject)=>{
        resolve('Remove garbage');
    })
}

let eatIceCream = function(p){
    return new Promise((resolve, reject)=>{
        resolve('Eat icecream');
    })
}

Promise.all([cleanRoom(), removeGarbage(), eatIceCream()]).then(function(){
    console.log('all finished');
});

Promise.race([cleanRoom(), removeGarbage(), eatIceCream()]).then(function(){
    console.log('one of the task is finished');
});